import React from 'react'
import {useDispatch,useSelector} from 'react-redux' 
import {selectedEventSelector,fetchCurrentEvent} from '../Store/modules/evntStore'
import {UserIsLogedSelector} from '../Store/modules/authStore'
import { CardActions, CardContent,Card,Button,Typography,Box,Grid,CardMedia} from '@material-ui/core'
import {useStyles} from '../ConfigMaterial'


const InvitShare = () => {
    const classes = useStyles()
    const dispatch = useDispatch()
    const currentEvnt = useSelector(selectedEventSelector)
    const userLog = useSelector(UserIsLogedSelector)
    const [display,setDisplay] = React.useState("hidden")

    React.useEffect(() => {

        const id = window.location.href.slice(32,52)
        console.log(id)
        dispatch(fetchCurrentEvent(id))

        window.fbAsyncInit = function() {
            window.FB.init({
              appId            : '851151665420013',
              autoLogAppEvents : true,
              xfbml            : true,
              version          : 'v9.0'
            });
        };

    },[dispatch])

    setTimeout(() => { setDisplay("visible")},1000)

    const RenderImage = () => {

        if(currentEvnt.image === null || undefined){

            return (null) 

        } else { return (<CardMedia className={classes.media}  image={currentEvnt.image} ></CardMedia>)}
    }

    const handleShareFb = () => {

        window.FB.ui({
            method: 'share',
            href: 'https://raffortville.github.io/WeddEasy'
        }, function(response){});
    }

    return(

       <Grid container style={{visibility: display}}>
           <Grid item md={2}>
               <Box>
                   <Typography color="secondary" variant="h5">Invitation</Typography>   
                   <div style={{width:'70%'}} className={classes.underLine}></div>
                </Box>
           </Grid>
           <Grid item md={5}>
               <Box  display="flex" justifyContent="center" alignItems="center" style={{height:"85vh"}}>
                   <Card style={{maxWidth:"345px"}} elevation={4}>
                      {currentEvnt.image && RenderImage()}
                       <CardContent>
                           <Typography gutterBottom variant="h5" component="h2">Lalalalala</Typography>
                           <Typography variant="body2" color="textSecondary" component="p">
                                Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging
                                across all continents except Antarctica
                            </Typography>
                       </CardContent>
                       {userLog  && 
                        <CardActions>
                            <Button onClick={handleShareFb} size="small" color="primary">Partage</Button>
                            <Button size="small" color="primary">Edit</Button>
                        </CardActions> 
                       }
                   </Card>
               </Box>
           </Grid>
       </Grid>
    )

}

export default InvitShare